}

.ss-simpleresponsive .caption {
-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
filter: alpha(opacity=0);
-moz-opacity: 0;
-khtml-opacity: 0;
opacity: 0;
position:absolute;
border: 0px;              
}

.fullwidthbanner img,
.ss-simpleresponsive > ul li,
.ss-simpleresponsive > ul li a,
.caption,
.caption a{
border: 0px !important
}

.slotholder{max-height: 690px;}

.slotholder > img {max-width:none;}
.caption.lfr.hide-for-small.ss-caption.start{height:auto !important;}

.fullwidthabanner ul {
